.sidebar {
    flex: 3;
    margin: 20px 0;
    padding-bottom: 30px;
    background-color: #fdfdfd;
    border-radius: 10px;
    display:flex;
    flex-direction: column;
    align-items: center;
}
.sidebarItem {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.sidebarTitle {
    margin: 10px ;
    padding: 15px;
    width: 80%;
    border-top: 1px solid #a7a4a4;
    border-bottom: 1px solid #a7a4a4;
    font-family: 'Varela Round', sans-serif;
    font-size: 12px;
    color: #222;
    font-weight: 600;
    line-height: 20px;
    text-align: center;
}
.sidebarItem > img {
    margin-top: 15px;
    width: 200px;
    height: 200px;
    border-radius: 100%;

}
.sidebarItem > p {
    padding: 30px;
}
.sidebarList {
    list-style: none;
    margin-bottom: 30px;
}
.sidebarListItem {
    display: inline-block;
    width: 50%;
    margin-top: 15px;
    cursor: pointer;
}
.sidebarSocial {
    margin-top: 15px;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.sidebarIcon {
    font-size: 16px;
    margin-left: 15px;
    cursor: pointer;
}