.header{
    margin-top: 60px;
}
.headerTitles {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Lora', serif;
    color:#444;
}
.headerTitleSm {
    position: absolute;
    top: 18%;
